.sc-cards{
    padding-bottom: 104px;
    h2{
        color: #fff;
        max-width: 891px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 50px;
        position: relative;
        z-index: 1;
    }
    .secondary-btn {
        display: inline-block;
        position: relative;
        background-color: #fff;
        color: #5F1CF7;
        border-radius: 50px;
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
        text-decoration: none;
        padding: 14px 58px 14px 28px;
        transition: 0.3s;
    }
    .secondary-btn .icon {
        width: 36px;
        height: 36px;
        border-radius: 50px;
        background-color: #5F1CF7;
        display: inline-block;
        position: absolute;
        right: 9px;
        top: 0;
        bottom: 0;
        margin: auto;
        transition: 0.3s;
    }
    .secondary-btn:hover{
        background-color: #5F1CF7;
        color: #fff;
    }
    .cards__box{
        display: inline-block;
        width: 100%;
        padding-top: 80px;
        padding-bottom: 84px;
        background-color: #171515;
        border-radius: 40px;
        padding-left: 85px;
        padding-right: 85px;
        position: relative;
        
    }
    .img__box{
        position: absolute;
        bottom: 66px;
        left: -70px;
        max-width: 530px;
        img{
            max-width: 100%;
        }
    }
    .img__box-2{
        position: absolute;
        bottom: -63px;
        right: -70px;
        img{
            max-width: 100%;
        }
    }
    .cards__wrap{
        display: flex;
        flex-wrap: wrap;
        margin-left: auto;
        margin-right: auto;
        justify-content: center;
        text-align: left;
        position: relative;
        z-index: 1;
    }
    .card{
        background-color: #262626;
        border-radius: 28px;
        margin-left: 28px;
        margin-right: 28px;
        width: calc(25% - 66px);
        padding: 60px 18px 18px 18px;
        font-family: 'roboto-condensed-semibold';
        font-weight: 600;
        position: relative;
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 10px;
        &.testCard{
            background-color: #601FEB;
            padding-top: 49px;
            .title{
                color: #fff;
                border: none;
            }
            .description{
                color: #A8A8A8;
                max-width: 170px;
            }
            .secondary-btn{
                background-color: #98FF7A;
                color: #000;
                .icon{
                    background-color: #fff;
                }
                &:hover{
                    background-color: #fff;
                }
            }
        }
        .top__info{
            align-self: flex-start;
        }
        .icon-arrow-4{
            position: absolute;
            top: 15px;
            right: 19px;
            width: 45px;
            height: 45px;
            display: flex;
            align-items: center;
            border-radius: 100%;
            background-color: #454545;
        }
        .title{
            font-size: 24px;
            line-height: 28px;
            color: #98FF7A;
            padding-left: 13px;
            padding-right: 13px;
            border-bottom: 1px solid #fff;
            padding-bottom: 10px;
            margin-bottom: 20px;
        }
        .description{
            font-size: 13px;
            line-height: 15px;
            color: #FFFFFF;
            padding-right: 20px;
            margin-bottom: 37px;
        }
        .secondary-btn{
            width: 100%;
            align-self: flex-end;
            
        }
    }
}

@media (max-width: 1333px) {
    .sc-cards{
        .img__box-2{
            bottom: -69px;
            right: -11px;
        }
    }
}
@media (max-width: 1280px) {
    .sc-cards{
        .card{
            width: calc(50% - 66px);
        }
    }
}
@media (max-width: 991px) {
    .sc-cards{
        h2{
            max-width: 565px;
        }
        .cards__box{
            padding-left: 10px;
            padding-right: 10px;
        }
        .img__box{
            position: absolute;
            bottom: 0;
            max-width: 364px;
            top: 0;
            display: flex;
            align-items: center;
        }
    }
}
@media (max-width: 767px) {
    .sc-cards{
        h2{
            font-size: 30px;
            line-height: 40px;
        }
        .cards__box {
            padding-top: 43px;
            padding-bottom: 47px;
            padding-left: 20px;
            padding-right: 20px;
        }
        .img__box{
            max-width: 187px;
        }
        .card{
            width: calc(50% - 10px);
            margin-left: 5px;
            margin-right: 5px;
        }
    }
}
@media (max-width: 576px) {
    .sc-cards{
        h2{
            margin-bottom: 34px;
        }
        .card{
            width: 100%;
            margin-left: 0;
            margin-right: 0;
        }
        .img__box-2 {
            bottom: -47px;
            right: -4px;
            max-width: 100px;
        }
    }
}