/* Inter-Bold */
@font-face {
	font-family: "Inter";
	src: url("./fonts/Inter-Bold.ttf") format("truetype");
	font-weight: 700;
	font-style: normal;
  font-display: swap;
}

/* Inter-SemiBold */
@font-face {
	font-family: "Inter";
	src: url("./fonts/Inter-SemiBold.ttf") format("truetype");
	font-weight: 600;
	font-style: normal;
  font-display: swap;
}

/* Inter-Medium */
@font-face {
	font-family: "Inter";
	src: url("./fonts/Inter-Medium.otf") format("opentype");
	font-weight: 500;
	font-style: normal;
  font-display: swap;
}

/* Inter-Medium */
@font-face {
	font-family: "KyivType-Sans-Medium";
	src: url("./fonts/KyivTypeSans-VarGX.ttf") format("truetype");
	font-style: normal;
  font-display: swap;
}
/* roboto-condensed-semibold */
@font-face {
	font-family: "roboto-condensed-semibold";
	src: url("./fonts/roboto-condensed-semibold.ttf") format("truetype");
	font-style: normal;
  font-display: swap;
}

html {
	font-family: "Inter";
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
}

h1{
  font-family: "KyivType-Sans-Medium";
  font-size: 64px;
  line-height: 106%;
  letter-spacing: 0.06em;
  color: #1D1D1D;
  -webkit-text-stroke: 1px #000000;
  margin-bottom: 25px;
}

h2{
  font-family: "KyivType-Sans-Medium";
  font-size: 48px;
  line-height: 58px;
}

