.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  transition: 0.3s;
}

.header.scrolling {
  box-shadow: 0px 0px 20px #171515;
  background-color: #fff;
}

.header .head__wrap {
  display: flex;
  align-items: center;
  height: 100px;
  flex-wrap: wrap;
}

.header a {
  transition: 0.3s;
}

.header .logo img {
  max-width: 100%;
}

.header nav {
  max-width: 700px;
  margin: auto;
  width: 100%;
}

.header .navbar {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

.header .navbar li {
  padding: 0 16px;
}

.header .navbar a {
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: #373737;
  text-decoration: none;
}

.header .navbar a:hover {
  color: #5F1FEC;
}

.header .login {
  margin-left: auto;
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}

.header .login a {
  color: #FFFFFF;
  display: inline-block;
  background-color: #5F1FEC;
  border-radius: 50px;
  text-decoration: none;
  position: relative;
  padding: 13px 17px 13px 47px;
}

.header .login a .icon {
  margin-right: 11px;
  text-decoration: none;
  position: absolute;
  top: 0;
  left: 14px;
  bottom: 0;
  margin: auto;
}

.header .login a:hover {
  background-color: #98FF7A;
}

.header .burger {
  width: 50px;
  height: 50px;
  border-radius: 4px;
  z-index: 10;
  position: relative;
  margin-left: 5px;
}
.header .burger > * {
  pointer-events: none;
}
.header .burger span {
  position: relative;
  margin-top: 9px;
  margin-bottom: 9px;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -5px;
  margin-top: -1.5px;
}
.header .burger span, .header .burger span::before, .header .burger span::after {
  display: block;
  width: 25px;
  height: 3px;
  border-radius: 5px;
  background-color: #5F1FEC;
  outline: 1px solid transparent;
  transition-property: background-color, transform;
  transition-duration: 0.3s;
}
.header .burger span::before, .header .burger span::after {
  position: absolute;
  content: "";
}
.header .burger span::before {
  top: -8px;
}
.header .burger span::after {
  top: 8px;
}
.header .burger.clicked span {
  background-color: transparent;
}
.header .burger.clicked span::before {
  transform: translateY(8px) rotate(45deg);
}
.header .burger.clicked span::after {
  transform: translateY(-8px) rotate(-45deg);
}
.header .burger.clicked span:before, .header .burger.clicked span:after {
  background-color: #fff;
}
.header .burger:hover {
  cursor: pointer;
}

@media (min-width: 1280px) {
  .header .container {
    max-width: 1616px;
  }
}
@media (max-width: 1280px) {
  .header nav {
    max-width: 524px;
  }
  .header .navbar {
    justify-content: center;
  }
}
@media (max-width: 991px) {
  .header .navbar li {
    width: 100%;
    margin: 20px 0;
  }
  .header .navbar a {
    color: #fff;
  }
  .header nav {
    background-color: #171515;
    position: fixed;
    z-index: 9;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    max-width: 100%;
    padding: 100px 40px 60px 40px;
    overflow-y: auto;
    transform: translateX(-100%);
    transition: transform 0.55s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  }
  .header nav.show {
    transform: translateX(0px);
  }
  .header nav.show ul li {
    transform: translateX(0px);
    opacity: 1;
  }
  .header nav.show ul li:nth-child(1) {
    transition-delay: 0.15s;
  }
  .header nav.show ul li:nth-child(2) {
    transition-delay: 0.3s;
  }
  .header nav.show ul li:nth-child(3) {
    transition-delay: 0.45s;
  }
  .header nav.show ul li:nth-child(4) {
    transition-delay: 0.6s;
  }
  .header nav.show ul li:nth-child(5) {
    transition-delay: 0.75s;
  }
  .header nav.show ul li:nth-child(6) {
    transition-delay: 0.9s;
  }
  .header nav.show ul li:nth-child(7) {
    transition-delay: 1.05s;
  }
  .header nav.show ul li:nth-child(8) {
    transition-delay: 1.2s;
  }
  .header nav.show ul li:nth-child(9) {
    transition-delay: 1.35s;
  }
  .header nav ul li a {
    text-transform: uppercase;
    padding: 10px 0;
    transition: all 0.3s ease;
  }
}
@media (max-width: 576px) {
  .header .head__wrap {
    height: 68px;
  }
  .header .logo {
    max-width: 120px;
  }
  .header .login {
    font-size: 17px;
    line-height: 25px;
  }
  .header .login a {
    padding: 8px 17px 8px 39px;
  }
  .header .login .icon {
    width: 15px;
    height: 19px;
  }
}