.sc-banner {
  padding-top: 50px;
  padding-bottom: 38px;
  position: relative;
}
.sc-banner .primary-btn {
  display: inline-block;
  position: relative;
  background-color: #5F1FEC;
  border-radius: 50px;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;
  text-decoration: none;
  padding: 14px 58px 14px 28px;
}
.sc-banner .primary-btn span {
  color: #FFFFFF;
  transition: 0.3s;
  position: relative;
  z-index: 1;
}
.sc-banner .primary-btn .icon {
  width: 41px;
  height: calc(100% - 6px);
  border-radius: 50px;
  background-color: #fff;
  display: inline-block;
  position: absolute;
  right: 3px;
  top: 0;
  bottom: 0;
  margin: auto;
  transition: 0.3s;
}
.sc-banner .primary-btn .icon:before {
  left: auto;
  right: 12px;
}
.sc-banner .primary-btn:hover span {
  color: #5F1FEC;
}
.sc-banner .primary-btn:hover .icon {
  width: calc(100% - 6px) !important;
}
.sc-banner .container {
  position: relative;
}
.sc-banner .bubble__img {
  position: absolute;
  z-index: -1;
  right: 124px;
  top: -180px;
  max-width: 840px;
}
.sc-banner .bubble__img img {
  max-width: 100%;
}
.sc-banner .head__info p {
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  color: #4D4D4D;
  margin-bottom: 33px;
}
.sc-banner .banner__details {
  width: 674px;
  display: flex;
  margin-left: auto;
  margin-top: 120px;
  justify-content: space-between;
  margin-bottom: -95px;
  z-index: 1;
  position: relative;
  min-height: 483px;
}
.sc-banner .phone-frame{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.sc-banner .detail__box {
  background-color: #98FF7A;
  border-radius: 16px;
  padding: 38px 12px 18px 12px;
  align-self: center;
  width: 179px;
  position: relative;
}
.sc-banner .detail__box  > * {
  transition: 0.3s ease-in-out;
}
.sc-banner .detail__box .icon-arrow-2 {
  top: 5px;
  right: 5px;
  width: 44px;
  height: 44px;
  border-radius: 100%;
  position: absolute;
  background-color: #D8FFC3;
  display: inline-block;
  transition: 0s;
}
.sc-banner .detail__box p {
  font-weight: 600;
  font-size: 24px;
  line-height: 18px;
  color: #000000;
  margin-bottom: 0;
  padding-bottom: 19px;
  border-bottom: solid 1px #fff;
  margin-bottom: 10px;
}
.sc-banner .detail__box p span {
  font-size: 13.6px;
  line-height: 16px;
  display: inline-block;
  width: 100%;
}
.sc-banner .detail__box .description {
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: #000000;
}
.sc-banner .detail__box .price__box {
  display: inline-block;
  width: 100%;
  border: solid 2px #98FF7A;
  border-radius: 12px;
  margin-bottom: 26px;
  margin-top: 10px;
  padding: 9px 10px 14px 10px;
  text-align: center;
}
.sc-banner .detail__box .price__box .price {
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
  margin-bottom: 4px;
}
.sc-banner .detail__box .price__box .installments {
  display: inline-block;
  width: 100%;
  font-weight: 600;
  font-size: 11px;
  line-height: 13px;
  color: #848484;
}
.sc-banner .detail__box .primary-btn {
  font-size: 12px;
  padding-left: 20px;
  width: 100%;
}

@media (min-width: 767px) {
  .sc-banner .swiper-slide{
      width: 192px;
      display: flex;
      align-items: center;
      cursor: grab;
  }
  .sc-banner .swiper-slide .detail__box {
    width: 179px;
    margin-left: auto;
    margin-right: auto;
  }
  .sc-banner .swiper-slide.swiper-slide-active{
    text-align: center;
  }
  .sc-banner .swiper-slide.swiper-slide-active .detail__box{
    background-color: transparent;
    padding-left: 0;
    padding-right: 0;
    border-radius: 0px;
    width: 100%;
    padding-top: 15px;

  }
  .sc-banner .swiper-slide .detail__box .detail__logo,
  .sc-banner .swiper-slide .detail__box .price__box,
  .sc-banner .swiper-slide .detail__box .primary-btn{
    display: none;
  }
  .sc-banner .swiper-slide.swiper-slide-active .detail__box .detail__logo,
  .sc-banner .swiper-slide.swiper-slide-active .detail__box .price__box,
  .sc-banner .swiper-slide.swiper-slide-active .detail__box .primary-btn{
    display: block;
  }
  .sc-banner .swiper-slide.swiper-slide-active .detail__box .icon-arrow-2{
    transform: scale(0);
  }
  .sc-banner .swiper-slide.swiper-slide-active .detail__box .title,
  .sc-banner .swiper-slide.swiper-slide-active .detail__box .title span{
    color: #805dd4;
    font-size: 21.7px;
    font-weight: 500;
    line-height: 11px;
    margin-bottom: 0px;
    margin-top: 15px;
  }
  .sc-banner .swiper-slide .detail__box .description{
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
  }
  .sc-banner .swiper-slide.swiper-slide-active .detail__box .description{
    font-weight: 600;
    font-size: 9.9px;
    line-height: 12px;
    color: #666666;
    margin-bottom: 6px;
    display: inline-block;
    -webkit-box-orient: none;
    -webkit-line-clamp: none;
    overflow: visible;
  }
  
}
@media (max-width: 1280px) {
  .sc-banner h1 {
    font-size: 46px;
  }
  .sc-banner .head__info {
    max-width: 351px;
  }
  .sc-banner .banner__details {
    margin-left: -205px;
    margin-top: 55px;
  }
  .sc-banner .bubble__img {
    right: 0px;
    top: -54px;
    max-width: 588px;
  }
}
@media (max-width: 991px) {
  .sc-banner .bubble__img {
    top: 0px;
    max-width: 390px;
  }
  .sc-banner .banner__details {
    width: 100%;
    margin-top: 30px;
    margin-right: auto;
    margin-left: auto;
  }
}
@media (max-width: 767px) {
  .sc-banner {
    overflow: hidden;
  }
  .sc-banner h1 {
    font-size: calc(4vw + 24px);
  }
  .sc-banner .primary-btn {
    width: 100%;
  }
  .sc-banner .bubble__img {
    top: -53px;
    max-width: 218px;
    right: 5px;
    opacity: 0.5;
  }
  .sc-banner .banner__details {
    flex-wrap: wrap;
    margin-top: 30px;
    margin-bottom: 0;
  }
  .sc-banner .detail__box {
    align-self: auto;
    margin-top: 0;
    width: calc(50% - 5px);
  }
  .sc-banner .detail__box .icon {
    width: 39px;
    height: 39px;
  }
  .sc-banner .detail__box{
    width: 100%;
    margin-bottom: 10px;
  }
}